.dpit-g-options {
	padding-left: 15px;

	h1 {
		text-transform: uppercase;

		.dpit-g-options__main-title {
			padding-bottom: 0.4em;

			strong {
				font-size: 1.1em;
				text-shadow: 0px 0px 1px rgba(var(--dpit-uac-prim-bg), 0.9);
				color: rgb(var(--dpit-uac-prim-bg)) !important;
			}

			a.dpit-g-options__v {
				//vertical-align: super;
				font-size: 0.7em;
				text-decoration: none;
				cursor: pointer;
				margin-left: 3px;
				text-transform: none;
				color: rgb(var(--dpit-uac-menu-a-item-bg)) !important;
				@include transitionFast(color);

				&:hover {
					color: rgb(var(--dpit-uac-prim-bg)) !important;
				}
			}
		}

		.dpit-g-options__sub-title {
			font-size: 0.95em;
		}
	}








	.nav-tab-wrapper {
		display: inline-block;
		padding-right: 20px;
		padding-top: 0;

		a {
			margin-top: 10px;

			&:first-child {
				margin-left: 0;
			}

			&.nav-tab-active {
				background-color: rgb(var(--dpit-uac-menu-a-item-bg));
				color: rgb(var(--dpit-uac-menu-a-item));
			}
		}
	}

	form {
		padding: 0 20px 0 0;
	}

	.form-table {

		td,
		th {
			padding: 15px 10px 10px 0;

			@media screen and (max-width: 782px) {
				padding: 10px 0 0;
			}
		}

		td {
			@media screen and (max-width: 782px) {
				padding-bottom: 10px;
			}
		}
	}

	&--license {
		table th {
			width: auto;
		}
	}


	&__desc {
		margin-top: 0;
		font-size: 1.1em;


		&--main {
			margin-top: 0;
			margin-bottom: 0.5em;
			max-width: unset;
			//color: rgb(var(--dpit-uac-badge-bg));
		}
	}

	&__width-fix {
		max-width: 60%;

		@include atS {
			max-width: 490px;
		}
	}

	&__heading-badge {
		vertical-align: super;
		transform: translateY(-30%);
		font-size: 0.6rem;
		font-weight: 500;
		letter-spacing: 1px;
		color: rgb(var(--dpit-uac-prim-bg)) !important;
		background-color: rgba(var(--dpit-uac-prim, 0.85)) !important;
		border: 1px solid rgb(var(--dpit-uac-prim-bg)) !important;
		display: inline-block;
		line-height: 1.7 !important;
		padding: 2px 8px;
		margin: 5px 0;
		text-transform: uppercase;
		text-decoration: none;
		border-radius: 2px;
		@include transitionFast(background-color);
		box-shadow: none !important;



		&:hover {
			background-color: rgb(var(--dpit-uac-prim)) !important;
		}

		&--free {
			color: rgb(var(--dpit-uac-badge)) !important;
			background-color: rgba(var(--dpit-uac-badge-bg, 0.85)) !important;
			border: 1px solid rgba(var(--dpit-uac-badge), 0.2) !important;

			&:hover {
				background-color: rgb(var(--dpit-uac-badge-bg)) !important;
			}
		}
	}

	&__action {

		margin-right: 12px !important;

		box-shadow: none;
		display: inline-block;
		vertical-align: middle;


		.dpit-admin-btn {
			max-width: 200px;
			min-width: 120px;

			&__anim {
				--bullet-anim-gap: 3px;
				--bullet-anim-off-alpha: 0.5;
			}
		}
	}
}