.dpit-settings {

	&__heading {
		text-transform: uppercase;
		font-size: 1.5em;
		margin-bottom: 3px;
		line-height: 1.6;
	}

	&__btn {
		margin: 5px 0 !important;
		text-transform: uppercase;

		&--news {
			display: block !important;
			max-width: 250px;
			border: none !important;
			text-transform: unset;

			&::hover {
				outline: 1px solid
			}
		}
	}

	&__cta-wrap {
		display: flex;
		gap: 1em;
		margin-bottom: 1.5em;
		flex-wrap: wrap;
	}

	&__news {
		border-radius: 2px;
		background-image: linear-gradient(120deg, rgb(var(--dpu-color-3)) 34.33%, rgb(var(--dpu-color-2)));
		//background-color: rgba(var(--dpu-color-4), 0.2);
		display: inline-flex;
		align-items: center;
		padding: 1em 1.5em 1em 1.5em;
		gap: 1em;


		@include atXXS {
			flex-direction: column;
			align-items: flex-start;
		}

		a {
			margin: 0 !important;

		}
	}

	&__news-label {
		font-size: 1.3em;
		color: rgb(var(--dpu-color-3-text));
	}

	&__c2r {
		background-color: $background-color-light;
		margin: 0;
		padding: 1em 1.5em 1em 1.5em;
		max-width: 240px;
	}

	&__spec-cfg {
		margin-bottom: 0.8em;
		display: flex;
		align-items: center;
		text-align: right;
		flex-wrap: wrap;
		gap: 0.5em;

		a.button-secondary {
			text-align: unset;
			display: block !important;
		}
	}


	&__sub-heading {
		display: block;
		font-size: 1.2em;
		margin-bottom: 20px;
		max-width: 700px
	}

	&__info {
		font-size: 1.05em;
	}



	&__el {
		@include atS {
			max-width: 80% !important;
			display: inline-block !important;
		}

		&[name="dp_it_general_options[theme]"] {
			@include atS {
				max-width: 200px !important;
				display: inline-block !important;
			}
		}

		&__hint {
			visibility: hidden;
		}
	}
}