.dpit-loader {
	--size: 12px;
	box-sizing: border-box;
	overflow: hidden;
	width: 100%;
	height: 100%;
	//cursor: default;
	cursor: wait;

	transition: opacity $transition-time-med ease;
	//background-color: rgba(var(--dpu-color-3-text), 0.9);
	z-index: 22;


	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;

	@include atS {
		width: 100vw;
		height: 100vh;
	}

	&__title {
		text-align: center;
		font-size: 1.2em;
		line-height: 1.3;
		padding-bottom: 1em;
		letter-spacing: 1px;
		color: rgb(var(--dpit-uac-prim-bg));
		font-weight: 700;
		text-transform: uppercase;
		max-width: 250px;

	}

	&__items {
		display: flex;
		align-items: center;
		justify-content: center;
		gap: calc(2 * var(--size));
	}



	&--fixed {
		position: fixed;
		top: 0;
		left: 0;
	}

	&--inactive {
		cursor: default;
		pointer-events: none;
		opacity: 0;
	}

	&__item {
		border-radius: 100%;
		border: var(--size) solid rgba(var(--dpit-uac-prim-bg, 0.95));
	}

	&--inactive {
		.dpit-loader__item {
			animation: unset !important;
		}

		pointer-events: none;
		opacity: 0;
	}

	&__item:nth-child(1) {
		animation: preloader .6s ease-in-out alternate infinite;
	}

	&__item:nth-child(2) {
		animation: preloader .6s ease-in-out alternate .1s infinite;
	}

	&__item:nth-child(3) {
		animation: preloader .6s ease-in-out alternate .2s infinite;
	}

	&__item:nth-child(4) {
		animation: preloader .6s ease-in-out alternate .3s infinite;
	}
}





@keyframes preloader {
	100% {
		transform: scale(1.6);
	}
}